.container {
}
.loader {
    width: 70%;
    margin: 0 auto;
}
.header {
    width: 70%;
    margin: 0 auto;
}
.bg_img {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
    opacity: 0.5;
}
.center {
    width: 70%;
    margin: 0 auto;
    border-left: 1px dashed white;
    border-right: 1px dashed white;
}
.detail {
    text-align: center;
    margin-top: 100px;
    margin: 130px 10% 100px;
}
.header h1 a {
    text-decoration: none;
    color: gold;
}
.img img {
    border: 4px solid #ffbd1b;
}
.title {
    font-size: 35px;
    font-style: italic;
    margin: 15px 0 10px;
}
.gen {
    display: flex;
    align-items: center;
    justify-content: center;
}

.gen ul {
    padding: 3px 14px 6px 14px;
    color: gold;
    background-color: black;
    border: 3px solid gold;
    border-radius: 30px;
    margin: 10px 5px 20px 0;
}

.des {
    font-weight: lighter;
    margin-top: 20px;
    font-family: "Courier New", Courier, monospace;
    text-align: justify;
}
.rr {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 60px;
}
.center_line {
    opacity: 0.5;
}
.rating_color {
    color: gold;
}
