.movie {
}

.movie_title {
    text-decoration: none;
}

.movie_title:hover {
    color: #ffbd1b;
}

.movie_img_box {
    width: 100%;
    position: relative;
}

.movie_img:hover {
    opacity: 0.7;
    transition: 0.5s;
}

.movie_img:hover + .movie_img_wrap {
    opacity: 1;
    transition: 0.5s;
}

.movie_img_wrap:hover {
    opacity: 1;
}

.movie_imgBtn {
    display: block;
    text-align: center;
    width: 80px;
    height: 30px;
    color: #fff;
    text-decoration: none;
    margin: 0 auto;
}

.movie_img_wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: 0.3s;
    background-color: rgba(000, 000, 000, 0.5);
    opacity: 0;
}

.movie_rating {
    text-align: center;
    color: gold;
    font-size: 30px;
    font-weight: 600;
}
